import { Component, OnInit } from '@angular/core';
import { RoadService } from '../../services/road/road.service';
import { RoadListDBService } from '../../services/DB/roadListDB';
import { UserService } from '../../services/user.service';
import { ParkingDictionary } from '../../services/constants/parkingDictionary';
import { DeviceService } from '../../services/device/device.service';

@Component({
  selector: 'app-menu-setRoad',
  templateUrl: './menu-setRoad.component.html',
  styleUrls: ['./menu-setRoad.component.scss']
})
export class MenuSetRoadComponent implements OnInit {
  /**tab */
  parking_panel = {
    isShow: false,
    parkingOpt: {
      // rechargeType: 0,       //
      // discountAllow: 0,      //
      // discountType: "",//
      // chargeDecimal: 0,//
      // monthlyOverFee: 0,//
      // isFreeTemporaryCard: 0,//
      // noCardRelease: 0,//
      // IDControlInOrOut: 0,//ID
      // IDNoGetRead: 0,//ID
      // temporaryCardOutCancelled: 0,//ID
      // realTimeDownloadValidCard: 0,//
      // monthCardExpireTipNum: 0,//
      // showShopTime: 0,//
      // broadcastCardNum: 0,//
      // timeLoadingType: 0,//
      // timeInterval: "",//
      // centerFeeTimeout: 0,//
      // centerTimeoutFee: "",//
      // showGateStatus: 0,//
      // showCardMachineStatus: 0,//
      // inOrOutTimeInterval: "",//
      // isCanFreeCars: 0,//
      // fullLightBox: "",//
      // fullDeviceNum: "",//
      // fullPort: 0,//
      // fullBanCardRead: 0,//
      // totalTruckSpaceNum: "",//
      // temporaryTruckSpaceNum: "", //
      // fixationTruckSpaceNum: "",//
      // showRemainingTruckSpaceNum: 0,//
      // rechargeScreen: "",//
      // closedScreenPort: "",//
      // showReleaseInformation: 0,//
      // controlShowWeek: 0,//
      // billPrinting: 0//
    },
    //
    discountMethods: this.parkdic.discountMethods,
    //
    feeMethods: this.parkdic.feeMethods,
    //      
    loadTimeMethods: this.parkdic.loadTimeMethods,
    /**/
    coverShow:false
  };
  road_panel = {
    isShow: false,
    roadArray: [],
    gateFlags: this.parkdic.gateFlags,
    cotrollers: [],
    gates: [],
    openMethods: this.parkdic.openMethods,
  };
  
  // roadArray;
  // /** */
  // flags;
  // /** */
  // cotrollers;
  // /** */
  // gates;
  // /** */
  // methods;
  /**/
  addObject={
    addcontroller:"",
    openWay:"",
    addRoadNumber:"",
    entranceAndExit_Identification:"",
    entranceAndExit:"",
    Monitor:false,
    checkCard:false,
    getCard:false,
    addControllerArray:[
      {
        label: '1',
        value: '1'
      },
      {
        label: '2',
        value: '2'
      }
    ]
  };
  // addcontroller:any;
  // openWay:any;
  // addRoadNumber:any;
  // entranceAndExit:any;
  // Monitor:any;
  // checkCard:any;
  // getCard:any;
  // addControllerArray=[
  //   {
  //     label: '1',
  //     value: '1'
  //   },
  //   {
  //     label: '2',
  //     value: '2'
  //   }
  // ];
  // addController
  constructor(private road: RoadService, private roadDB: RoadListDBService, private user: UserService, private parkdic: ParkingDictionary,private device: DeviceService) {
    // this.road_panel.roadArray = [];
    //
    this.road.roadList$.subscribe((roadObj) => {
      this.road_panel.roadArray = roadObj.collection;
      //
      this.roadDB.clearDB();
      roadObj.collection.forEach(element => {
        this.roadDB.addData(element);
      });
    });
    //
    this.road.parkOptList$.subscribe((parkOptList) => {
      this.parking_panel.parkingOpt = parkOptList;
    });
        /**/
        this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op === "ret-hb").subscribe((data) => {
          this.road_panel.cotrollers = [];
          data.hw.forEach(element => {
            this.road_panel.cotrollers.push(element.hw_id);
          });
        });
    // this.device.cotrollers
  }

  ngOnInit() {
    this.user.isLogin = false;
    this.parking_panel.isShow = true;
    this.road.getParkingOptList();
    this.road.getRoadList(1);
  }
  add(){
    console.log(this.addObject);

    this.road.getParksettingAddData(this.addObject);
  }
  submit() {
    console.log(this.road_panel.roadArray);
  }

  getList() {
    this.road.getRoadList(1);
    console.log(1221);

  }

  /**/
  changeTabs(labelName) {
    this.closeAllTabs();
    switch (labelName) {
      case 'parking_panel':
        this.parking_panel.isShow = true;
        return;
      case 'road_panel':
        this.road_panel.isShow = true;
        return;
      default:
        this.parking_panel.isShow = true;
        return;
    }

  }
  closeAllTabs() {
    this.parking_panel.isShow = false;
    this.road_panel.isShow = false;
  }
}



