import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { RenderService } from '../render/render.service';

@Injectable()
export class OutparkdbService {
    private db: any = null;
    private connection = indexedDB.open('outParkList_DB', 1);
    private storeName = "outPark";

    /** */
    outParkList$ = new Subject<Array<outParkItem>>();
    constructor(private render: RenderService) {
        this.connection.onupgradeneeded = function (event) {
            this.db = event.target.result;
            /** */
            this.db.createObjectStore(this.storeName, { keyPath: 'id', autoIncrement: true });
        }.bind(this);
        //
        this.connection.onsuccess = function (event) {
            console.log("Success!");
            this.db = event.target.result;
        }.bind(this);
        //
        this.connection.onerror = function (e) {
            console.log("Error");
            console.dir(e);
        }.bind(this);
    }

    /** */
    addData(data: outParkItem) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let req = store.add(data);
        req.onsuccess = (evt) => {
            this.getList();
        };
        req.onerror = function () {
            console.error("add error", this.error);
        };
    }

    /** */
    addDatas(datas: Array<outParkItem>) {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        datas.forEach((data) => {
            let req = store.add(data);
            req.onsuccess = (evt) => {
                this.getList();
            };
            req.onerror = function () {
                console.error("add error", this.error);
            };
        });
    }

    /** */
    getList() {
        if (!this.db) {
            return;
        }
        let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
        /** */
        let len;
        let req = store.count();
        req.onsuccess = (evt) => {
            len = evt.target.result;
            let outParkList = [];
            if (len == 0) {
                this.outParkList$.next(outParkList);
            }
            else {
                let request = store.openCursor();
                request.onsuccess = (evt) => {
                    let cursor = evt.target.result;
                    if (cursor) {
                        let result = cursor.value;
                        let item: outParkItem = {
                            adminUserId: result.adminUserId,
                            parkId: result.parkId,
                            createId: result.createId,
                            createName: result.createName,
                            createTime: result.createTime,
                            updateTime: result.updateTime,
                            updateId: result.updateId,
                            updateName: result.updateName,
                            cardID: result.cardID, //ID
                            cardNo: result.cardNo, //
                            cardType: result.cardType, // 
                            carNumber: result.carNumber, // 
                            ownerName: result.ownerName, // 
                            comeTime: result.comeTime, // 
                            comePark: result.comePark, // 
                            gateNumber: result.gateNumber, // 
                            comePhoto: result.comePhoto, // 
                            certificatePhoto: result.certificatePhoto, // 
                            cardBalance: result.cardBalance, // 
                            outTime: result.outTime, // 
                            outPark: result.outPark, // 
                            outGateNumber: result.outGateNumber, // 
                            outPhoto: result.outPhoto, // 
                            feeMoney: result.feeMoney, // 
                            amountReceive: result.amountReceive, // 
                            timeoutFlag: result.timeoutFlag, // 
                            timeoutHour: result.timeoutHour, // 
                            timeoutMoney: result.timeoutMoney, // 
                            freeReason: result.freeReason, // 
                            freeReasonId: result.freeReasonId, // Id
                            remark: result.remark, // 
                            recordId: result.recordId
                        };
                        outParkList.push(item);
                        cursor.continue();
                        if (outParkList.length == len) {
                            this.outParkList$.next(outParkList);
                            this.exportData(outParkList);
                        }
                    }
                    request.onerror = function (evt) {
                        console.error("add error", this.error);
                    };
                }
            }
        }
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };
    }

    /** */
    query(record_id) {
        return Observable.create((ob) => {
            let store = this.db.transaction(this.storeName, 'readonly').objectStore(this.storeName);
            let len;
            let req = store.count();
            req.onsuccess = (evt) => {
                len = evt.target.result;
                let isExist = false;
                if (len == 0) {
                    ob.next(isExist);
                }
                else {
                    let request = store.openCursor();
                    let querynumber = 0;
                    request.onsuccess = (evt) => {
                        let cursor = evt.target.result;
                        if (cursor) {
                            let result = evt.target.result.value;
                            if (record_id == result.recordId && isExist == false) {
                                isExist = true;
                                ob.next(isExist);
                                querynumber += 1;
                            }
                            else if (record_id != result.recordId || isExist == true) {
                                querynumber += 1;
                            }
                            if (querynumber == len && isExist == false) {
                                ob.next(isExist);
                            }
                            cursor.continue();
                        }
                    }
                    request.onerror = (evt) => {
                        ob.next(isExist);
                    }
                }
            }
            req.onerror = function (evt) {
                console.error("add error", this.error);
            };
        });
    }

    /** */
    deleteDatas(record_id) {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        let len;
        let req = store.count();
        req.onsuccess = (evt) => {
            len = evt.target.result;
            let isExist = false;
            if (len == 0) {
                return;
            }
            else {
                let request = store.openCursor();
                let querynumber = 0;
                request.onsuccess = (evt) => {
                    let cursor = evt.target.result;
                    if (cursor) {
                        let result = evt.target.result.value;
                        if (record_id == result.recordId && isExist == false) {
                            let dereq = cursor.delete();
                            querynumber += 1;
                            dereq.onsuccess = () => {
                            };
                            dereq.onerror = () => {
                                console.log("");
                            };
                        }
                        else if (record_id != result.recordId || isExist == true) {
                            querynumber += 1;
                        }
                        if (querynumber == len) {
                        }
                        cursor.continue();
                    }
                }
                request.onerror = (evt) => {
                }
            }
        }
        req.onerror = function (evt) {
            console.error("add error", this.error);
        };
    }

    /** */
    clearDB() {
        let store = this.db.transaction(this.storeName, 'readwrite').objectStore(this.storeName);
        store.clear();
        this.getList();
    }

        /** */
        exportData(datas) {
            let data = {
                list: datas
            }
            this.render.send('save-db-outpark', JSON.stringify(data));
        }
}

export interface outParkItem {
    // cardID: string, // ID
    // cardNo: string, // 
    // cardType: string, // 
    // carNumber?: string, // 
    // ownerName?: string, // 
    // comeTime: string, // 
    // comePark?: string, // 
    // gateNumber?: string, // 
    // comePhoto?: string, // 
    // certificatePhoto?: string, // 
    // cardBalance?: string, // 
    // remark?: string //    
    adminUserId: string,
    parkId: string,
    createId: string,
    createName: string,
    createTime: number,
    updateTime: number,
    updateId: string,
    updateName: string,
    recordId?: any,
    cardID?: string, //ID
    cardNo: string, //
    cardType: string, // 
    carNumber?: string, // 
    ownerName?: string, // 
    comeTime: number, // 
    comePark?: string, // 
    gateNumber?: string, // 
    comePhoto?: string, // 
    certificatePhoto?: string, // 
    cardBalance?: number, // 
    outTime: number, // 
    outPark: string, // 
    outGateNumber: string, // 
    outPhoto?: string, // 
    feeMoney: number, // 
    amountReceive: number, // 
    timeoutFlag?: number, // 
    timeoutHour?: number, // 
    timeoutMoney?: number, // 
    freeReason?: string, // 
    freeReasonId?: string, // Id
    remark?: string // 
}


