import { Component, OnInit } from '@angular/core';
import { ModalService } from '../../services/modal/modal.service';
import { UserService } from '../../services/user.service';

@Component({
  selector: 'app-interceptor',
  templateUrl: './interceptor.component.html',
  styleUrls: ['./interceptor.component.scss']
})
export class InterceptorComponent implements OnInit {

  constructor(private modal: ModalService,private user: UserService) { }

  /** */
  buttonWord = "";
  isOperable = true;
  ngOnInit() {
    this.modal.isOperable$.subscribe((isAble) => {
      if (isAble == true) {
        this.buttonWord = "";
        this.isOperable = true;
      }
      else {
        this.buttonWord = `....`;
        this.isOperable = false;
      }
    });
  }

  login(password) {
    this.modal.isOperable$.next(false);
    this.user.interceptorLogin(password.value);
    setTimeout(() => {
      if (this.isOperable == false) {
        this.modal.isOperable$.next(true);
        alert("");
      }
    }, 5000);
  }

}



