import { Component, OnInit } from '@angular/core';
import { CardManageService } from '../../services/cardManage/cardManage.service';
import { CardListDBService } from '../../services/DB/cardListDB';

@Component({
  selector: 'app-searchCard',
  templateUrl: './searchCard.component.html',
  styleUrls: ['./searchCard.component.scss']
})
export class SearchCardComponent implements OnInit {
  map = {
    cardNo:"", 
    carNumber:"",
    cardType:"",
    ownerName:"",
    cardStatus:"",
    expireDays:""
  }

  constructor(private card: CardManageService,private cardDB:CardListDBService) { }

  head;
  body;

  /** */
  currentIndex: number = 1;
  /**6 */
  len: number = 6;
  /** */
  minIndex;
  /** */
  maxIndex;
  /** */
  maxPage;
  /** */
  totalSize;
  /** */
  pageIndex: Array<number> = [];

  search() {
    this.card.getCardInfo(1,"query",this.map);
  }

  ngOnInit() {
    // this.head = ['', '', '', '', '', '', '', '','', '',  ''];
    this.head = ['', '', '', '', '','',''];
    this.card.cardInfo$.subscribe((data: any) => {
      this.body = data.collection;
      this.maxPage = Number(data.totalPage);
      this.totalSize = data.totalSize;
      this.len = Number(data.totalPage) > this.len ? this.len : Number(data.totalPage);
      this.currentIndex == 1?this.initPageIndex(this.len,1):false;
    });

  }

  reset() {
    this.map = {
      cardNo:"", 
      carNumber:"",
      cardType:"",
      ownerName:"",
      cardStatus:"",
      expireDays:""
    }
    this.card.getCardInfo(1,"query",this.map);        
  }

  /** */
  initPageIndex(len: number, index = 1) {
    index = index < 1 ? 1 : index;
    this.minIndex = index;
    for (let i = 0; i < len; i++) {
      this.pageIndex[i] = index++;
      this.maxIndex = this.pageIndex[i];
    }
  }
  /** */
  indexNav(i, mode = "") {
    if (mode == "") {
      this.currentIndex = i;
    }
    else if (mode == "back") {
      this.currentIndex - 1 >= 1 ? this.currentIndex -= 1 : this.currentIndex = 1;
    }
    else if (mode == "next") {
      this.currentIndex + 1 <= this.maxPage ? this.currentIndex += 1 : this.currentIndex = this.maxPage;
    }
    // this.parking.getParkingCarChargeList(this.currentIndex);
    this.card.getCardInfo(this.currentIndex,"query",this.map);
    //
    if (this.currentIndex >= this.maxIndex) {
      this.initPageIndex(this.len, this.currentIndex);
    }
    //    
    else if (this.currentIndex <= this.minIndex) {
      this.initPageIndex(this.len, this.currentIndex - this.len + 1);
    }
    //
    if (this.currentIndex + this.len > this.maxPage) {
      this.initPageIndex(this.len, this.maxPage - this.len + 1);
    }
  }

}



