import { Component, OnInit } from '@angular/core';
import { DeviceService } from '../../services/device/device.service';
import { RoadListDBService } from '../../services/DB/roadListDB';
import { CardListDBService } from '../../services/DB/cardListDB';
import { ParkingDictionary } from '../../services/constants/parkingDictionary';
import { ParkingService } from '../../services/parking/parking.service';
import { ParkLotService } from '../../services/parkLot/parkLot.service';
import { InparkdbService, inParkItem } from '../../services/DB/inparkListDB';
import { OutparkdbService, outParkItem } from '../../services/DB/outparkListDB';
import { PrivilegedbService } from '../../services/DB/privilegeListDB';
import { PrivilegeService } from '../../services/privilege/privilege.service';
import { Observable } from 'rxjs/Observable';
import { CommonService } from '../../services/common.service';

@Component({
  selector: 'app-main-carinfo',
  templateUrl: './main-carinfo.component.html',
  styleUrls: ['./main-carinfo.component.scss']
})
export class MainCarinfoComponent implements OnInit {
  /** */
  templateCardArray: Array<any> = [];
  /** */
  templateCardItem = {
    cardNo: "",
    cardType: "",
    comeTime: "",
    outTime: "",
    outPark: "",
    outGateNumber: "",
    feeMoney: null,
    amountReceive: null,
    carNumber: "",
    privilege_1:"",
    privilege_2:"",    
    freeTime_1:0,
    freeName_1:"",
    useUnit_1:1,
    freeTime_2:0,
    freeName_2:"",    
    useUnit_2:1,
    controllerNo: "",    
    freeReason:"",

    // privilege_1: {name:""},
    // privilege_2: {name:""},
  };

  /** */
  privilegeNameList_1 = [];
  privilegeNumberList_1 = [];
  privilegeNameList_2 = [];
  privilegeNumberList_2 = [];

  clearPrivilege(){
    this.templateCardItem.privilege_1 = "";
    this.templateCardItem.privilege_2 = "";  
    this.templateCardItem.freeTime_1 = 0;
    this.templateCardItem.freeName_1= "";
    this.templateCardItem.useUnit_1= 1;
    this.templateCardItem.freeTime_2= 0;
    this.templateCardItem.freeName_2= "";    
    this.templateCardItem.useUnit_2= 1;
    this.privilegeNameList_2 = [];
    this.privilegeNumberList_2 = [];
  }

  /** */
  carInfo = {
    account: "",
    ownerName: "",
    cardNo: "",
    companyName: "",
    cardType: "",
    carNumber: "",
    gateNumber: "",
    outGateNumber: "",
    comeTime: "",
    outTime: "",
    feeMoney: "",
    nothing: "",
    controllerNo: "",
    record_id: ""
  }
  constructor(private device: DeviceService, private roadDB: RoadListDBService,
    private cardDB: CardListDBService, public parkingDictionary: ParkingDictionary,
    private parkLot: ParkLotService, private inpark: InparkdbService, private outpark: OutparkdbService,
    private parking: ParkingService, private privilegeDB: PrivilegedbService, private privilege: PrivilegeService,
    private common:CommonService) {
    /** */
    this.privilegeDB.privilegeList$.subscribe((list) => {
      this.privilegeNameList_1 = list;
    });
    /** */
    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op == "ret-time-to-money").subscribe((data)=>{
      this.templateCardItem.feeMoney = data.money;
      this.templateCardItem.amountReceive = data.money;      
    });
    /** */
    this.device.onmessage$.filter(data => data.ret == "0").filter(data => data.op == "report-card-record").debounceTime(500).subscribe((data) => {
      // 
      if (data.hw_type == 1) {
        //
        this.roadDB.getEntryNameByEntryMarking(data.hw_id).subscribe((name) => {
          //
          this.clearCarInfo();
          this.trans(data.card_id);
          this.carInfo.cardNo = data.card_id;
          this.carInfo.comeTime = data.time_in;
          this.carInfo.controllerNo = data.hw_id;
          this.carInfo.record_id = data.record_id;
          //
          if (name.isExist == true) {
            this.carInfo.outGateNumber = name.result;
          }
          else {
            this.carInfo.outGateNumber = data.hw_id;
          }
          //
          this.dataFormatToInparkList(data, this.carInfo.outGateNumber).subscribe((fulldata) => {
            this.inpark.query(fulldata.record_id).subscribe((isExist) => {
              //
              if (isExist == true) {
                this.device.sendBackRecord(this.carInfo.record_id);
              }
              //
              else if (isExist == false) {
                this.inpark.addData(fulldata);
                this.device.sendBackRecord(this.carInfo.record_id);
              }
              //
              this.parking.addInParkList([fulldata]);
              //        
              this.inParkUpdadeInfo(this.carInfo.cardType);
            });
          });
        });

      }
      //
      else if (data.hw_type == 2) {
        //        
        this.privilegeDB.getList();        
        //
        this.roadDB.getEntryNameByEntryMarking(data.hw_id).subscribe((name) => {
          //
          this.clearCarInfo();
          this.trans(data.card_id);
          this.carInfo.cardNo = data.card_id;          
          this.carInfo.comeTime = data.time_in;
          this.carInfo.outTime = data.time_out;
          this.carInfo.feeMoney = data.cost;
          this.carInfo.controllerNo = data.hw_id;
          this.carInfo.record_id = data.record_id;
          //
          if (name.isExist == true) {
            this.carInfo.outGateNumber = name.result;
          }
          else {
            this.carInfo.outGateNumber = data.hw_id;
          }
          //
          this.dataFormatToOutparkList(data, this.carInfo.outGateNumber).subscribe((fulldata) => {
            this.outpark.query(fulldata.recordId).subscribe((isExist) => {
              //
              if (isExist == true) {
                this.device.sendBackRecord(this.carInfo.record_id);
              }
              //
              else {
                this.outpark.addData(fulldata);
                this.device.sendBackRecord(this.carInfo.record_id);
              }
              //
              // this.parking.addOutParkList([fulldata]);
              //
              // let temp = this.templateCardItem;
              // Object.assign(temp,fulldata);
              fulldata.carNumber = this.carInfo.carNumber;              
              this.templateCardArray.push(fulldata);
              this.refreshTemplateCardArray();
              //        
              this.outParkUpdadeInfo(this.carInfo.cardType);
            });
          });
        });
      }
      else {
        this.clearCarInfo();
        alert('');
      }
    });

    //
    this.parking.inParkListBack$.subscribe((data) => {
      //
      if (data.isConnect == true) {
        data.data.forEach(element => {
          this.inpark.deleteDatas(element.recordId);
        });
        this.inpark.inParkList$.next([]);
        //
        this.inpark.inParkList$.filter(data => data.length > 0).subscribe((inParkList) => {
          // this.parking.addInParkList(inParkList);
        });
        this.inpark.getList();
      }
      //
      else {

      }
      // data
    });

    //
    this.parking.outParkListBack$.subscribe((data) => {
      //
      if (data.isConnect == true) {
        data.data.forEach(element => {
          this.outpark.deleteDatas(element.recordId);
        });
        this.outpark.outParkList$.next([]);
        //
        this.outpark.outParkList$.filter(data => data.length > 0).subscribe((outParkList) => {
          // this.parking.addOutParkList(outParkList);
        });
        this.outpark.getList();
      }
      //
      else {

      }
      // data
    });
  }

  ngOnInit() {
  }

  /** */
  clearCarInfo() {
    this.carInfo = {
      account: "",
      ownerName: "",
      cardNo: "",
      companyName: "",
      cardType: "",
      carNumber: "",
      gateNumber: "",
      outGateNumber: "",
      comeTime: "",
      outTime: "",
      feeMoney: "",
      nothing: "",
      controllerNo: "",
      record_id: ""
    }
  }

  trans(CardNo) {
    // this.roadDB.getEntryNameByEntryMarking("1").subscribe((name) => {
    //   console.log(name);
    // });
    this.cardDB.getCardInfoByCardNo(CardNo).subscribe((info) => {
      this.carInfo.account = info.account;
      this.carInfo.ownerName = info.ownerName;
      this.carInfo.companyName = info.companyName;
      this.carInfo.cardType = info.cardType;
      this.carInfo.carNumber = info.carNumber;
    });
  }
  /**/
  toRemoveTemplateItem() {
    if (this.templateCardArray.length > 0) {
      this.templateCardArray.pop();
      // this.templateCardArray.splice(this.templateCardArray.length-1,1);
      this.refreshTemplateCardArray();
    }
  }
  /**/
  refreshTemplateCardArray() {
    /** */
    this.clearPrivilege();          
    this.privilegeDB.getList();
    if (this.templateCardArray.length >= 1) {
      this.templateCardItem = this.templateCardArray[this.templateCardArray.length - 1];
    }
    console.log(this.templateCardItem);
    console.log(this.templateCardArray);
    
  }

  /** */
  openTheGate(hw_id) {
    this.device.sendOpenGate(hw_id);
    this.parking.addOutParkList([this.templateCardItem]);
    this.toRemoveTemplateItem();
    // this.playVoice();
  }

  /** */
  recount(event,mode,index) {
    if(mode == 1){
      //
      if(index == 1){
        this.privilegeNameList_1.forEach((item:any)=>{
          //
          if(item.freeName == event){
            this.privilegeNumberList_1.length = item.singleUseMax+1;
            this.templateCardItem.freeTime_1 = Number(item.freeTime);
            this.templateCardItem.freeName_1 = item.freeName;         
            this.templateCardItem.useUnit_1 = 1;                    
            
            //
            this.templateCardItem.privilege_2 = "";  
            this.templateCardItem.freeTime_2= 0;
            this.templateCardItem.freeName_2= "";    
            this.templateCardItem.useUnit_2= 1;
            this.privilegeNameList_2 = [];
            this.privilegeNumberList_2 = [];
            //
            if(item.superpositionType){
              this.privilegeNameList_2.length = 0;
              this.privilegeNameList_2 = [item.superpositionType];         
            }
            else{
              this.privilegeNameList_2.length = 0;
            }
          }
        });
      }
      //
      else if(index == 2){
        this.templateCardItem.useUnit_1 = Number(event);        
      }
    }
    else if(mode == 2){
      if(index == 1){
        this.privilegeNameList_1.forEach((item:any)=>{
          //
          if(item.freeName == event){
            this.privilegeNumberList_2.length = item.singleUseMax+1;
            this.templateCardItem.freeTime_2 = Number(item.freeTime);
            this.templateCardItem.freeName_2 = item.freeName;
            this.templateCardItem.useUnit_2 =  1;
          }
        });
      }
      else if(index == 2){
        // console.log(""+event);
        
        this.templateCardItem.useUnit_2 =  Number(event);
      }
    }
    // JSON.parse(localStorage.feeStandard).freeMinutes
    //30

    this.templateCardItem.freeReason = this.getFreeReason(this.templateCardItem.freeName_1,this.templateCardItem.useUnit_1,this.templateCardItem.freeName_2,this.templateCardItem.useUnit_2);
    //
    if( this.device.msToMin(Number(this.templateCardItem.outTime)-Number(this.templateCardItem.comeTime)) > Number(JSON.parse(localStorage.feeStandard).freeMinutes)){
      let a = this.templateCardItem.freeTime_1?this.templateCardItem.freeTime_1:0;
      let b = this.templateCardItem.useUnit_1&&Number(this.templateCardItem.useUnit_1)?this.templateCardItem.useUnit_1:0;
      let c = this.templateCardItem.freeTime_2?this.templateCardItem.freeTime_2:0;
      let d = this.templateCardItem.useUnit_2&&Number(this.templateCardItem.useUnit_2)>0?this.templateCardItem.useUnit_2:0;
      let PrivilegeTime = a*b+c*d;
      // console.log(PrivilegeTime);

      this.device.sendGetRealFee(this.device.msToMin(Number(this.templateCardItem.outTime) - Number(this.templateCardItem.comeTime)) - PrivilegeTime);
      // console.log(this.device.msToMin(Number(this.templateCardItem.outTime) - Number(this.templateCardItem.comeTime)) - PrivilegeTime);
      
    }
    

  }

  /** */
  sendDataToCloud(data) {
    //
    this.dataFormatToOutparkList(data, this.carInfo.outGateNumber).subscribe((fulldata) => {
      this.outpark.query(fulldata.recordId).subscribe((isExist) => {
        //
        if (isExist == true) {
          this.device.sendBackRecord(this.carInfo.record_id);
        }
        //
        else {
          this.outpark.addData(fulldata);
          this.device.sendBackRecord(this.carInfo.record_id);
        }
        //
        this.parking.addOutParkList([fulldata]);
      });
    });
  }

  /** */
  inParkUpdadeInfo(cardType) {
    if (cardType == "") {
      this.parkLot.addTemporaryCard();
    }
    else if (cardType == "") {
      this.parkLot.addMonthCardNum();
    }
    this.parkLot.decRemaining();
  }

  /** */
  outParkUpdadeInfo(cardType) {
    if (cardType == "") {
      this.parkLot.decTemporaryCard();
    }
    else if (cardType == "") {
      this.parkLot.decMonthCardNum();
    }
    this.parkLot.decRemaining();
  }

  /** */
  dataFormatToInparkList(oldData, roadName): Observable<any> {
    let adminUser = JSON.parse(localStorage.adminUser);
    let nowTime = new Date().getTime();
    return Observable.create((ob) => {
      let newData: inParkItem = {
        adminUserId: adminUser.adminUserId,
        parkId: adminUser.parkId,
        createId: adminUser.createId,
        createName: adminUser.createName,
        createTime: nowTime, //+adminUser.createTime,
        updateTime: nowTime, //+adminUser.updateTime,
        updateId: adminUser.updateId,
        updateName: adminUser.updateName,
        cardNo: oldData.card_id, // 
        cardType: this.device.parkdic.getLabelByKey(oldData.card_type), // 
        comeTime: this.device.timeStringToNumber(oldData.time_in), // ,number
        comePark: roadName, // 
        gateNumber: oldData.hw_id, // 
        recordId: oldData.record_id
      }
      ob.next(newData);
    });
  }
  //
  dataFormatToOutparkList(oldData, roadName): Observable<any> {
    let adminUser = JSON.parse(localStorage.adminUser);
    let nowTime = new Date().getTime();
    return Observable.create((ob) => {
      let newData: outParkItem = {
        adminUserId: adminUser.adminUserId,
        parkId: adminUser.parkId,
        createId: adminUser.createId,
        createName: adminUser.createName,
        createTime: nowTime, //+adminUser.createTime,
        updateTime: nowTime, //+adminUser.updateTime,
        updateId: adminUser.updateId,
        updateName: adminUser.updateName,
        cardNo: oldData.card_id, // 
        cardType: this.device.parkdic.getLabelByKey(oldData.card_type), // 
        comeTime: this.device.timeStringToNumber(oldData.time_in), // ,number
        outTime: this.device.timeStringToNumber(oldData.time_out), // 
        outPark: roadName, // 
        outGateNumber: oldData.hw_id, // 
        feeMoney: oldData.cost, // 
        amountReceive: oldData.cost, // 
        freeReason:"",
        recordId: oldData.record_id
      }
      ob.next(newData);
    });
  }

  /**
   * 
   * @param outparkItem 
   * @param feeMoney 
   */
  discountCost(outparkItem: outParkItem, feeMoney): Observable<any> {
    return Observable.create((ob) => {
      // let data = outparkItem;
      outparkItem.feeMoney = feeMoney;
      outparkItem.amountReceive = feeMoney;
      ob.next(outparkItem);
    });
  }

  /** */
  getFreeReason(ticket_1,number_1,ticket_2,number_2){
    if(ticket_1&&number_1){
      if(ticket_2&&number_2){
        console.log(`${ticket_1}${number_1}${ticket_2}${number_2}`);       
        return `${ticket_1}${number_1}${ticket_2}${number_2}`;
      }
      else{
        console.log(`${ticket_1}${number_1}`);        
        return `${ticket_1}${number_1}`;
      }
    }
    else{
      return "";
    }
  }

  /** */
  playVoice(){
    let dayOpt = this.common.timeTransform(Number(this.templateCardItem.outTime)-Number(this.templateCardItem.comeTime));
    // console.log(dayOpt);
    
    // if(data.feeMoney){
      // if(data.outGateNumber){
        this.device.sendRequestVoice(this.templateCardItem.outGateNumber,this.templateCardItem.feeMoney,dayOpt);
      // }
    // }
  }
}



