import { Component, OnInit } from '@angular/core';
import { ParkingService } from '../../services/parking/parking.service';
import { ReleasedbService, releaseItem } from '../../services/DB/releaseListDB';
import { RenderService } from '../../services/render/render.service';

@Component({
  selector: 'app-menu-updateRelease',
  templateUrl: './menu-updateRelease.component.html',
  styleUrls: ['./menu-updateRelease.component.scss']
})
export class MenuUpdateReleaseComponent implements OnInit {
  body;
  head = ['', '', '', '', '', '', ''];

  constructor(private release: ReleasedbService, private parking: ParkingService, public render: RenderService) {
    this.release.releaseList$.subscribe((release) => {
      this.body = release;
      this.exportData(release);
    });
  }

  /** */
  exportData(datas) {
    let data = {
      list: datas
    }
    this.render.send('save-db-release', JSON.stringify(data));
  }

  ngOnInit() {
    this.release.getList();
  }
  addData() {
    let adminUser = JSON.parse(localStorage.adminUser);
    let comm = new Date().getTime();

    let data: releaseItem = {
      adminUserId: adminUser.adminUserId,
      parkId: adminUser.parkId,
      createId: adminUser.createId,
      createName: adminUser.createName,
      createTime: adminUser.createTime, //+adminUser.createTime,
      updateTime: adminUser.updateTime, //+adminUser.updateTime,
      updateId: adminUser.updateId,
      updateName: adminUser.updateName,
      cardID: "1001", //ID
      cardNo: "2002", //
      cardType: "", // 
      carNumber: "100010", // 
      ownerName: "hello", // 
      comeTime: comm, // 
      comePark: "1", // 
      gateNumber: "11s", // 
      comePhoto: "result.comePhoto", // 
      certificatePhoto: "result.certificatePhoto", // 
      cardBalance: 42355, // 
      remark: "result.remark", // 
      recordId: `${"11s"}+${comm}` // 

    }

    this.release.addData(data);

  }

  sendData() {
    this.parking.addReleaseList(this.body);
  }

  clear() {
    this.release.clearDB();
  }



}



