import { Component, OnInit } from '@angular/core';
import { ParkingService } from '../../services/parking/parking.service';
import { DeviceService } from '../../services/device/device.service';
import { RoadListDBService } from '../../services/DB/roadListDB';
import { InparkdbService,inParkItem } from '../../services/DB/inparkListDB';

@Component({
  selector: 'app-menu-updateInParking',
  templateUrl: './menu-updateInParking.component.html',
  styleUrls: ['./menu-updateInParking.component.scss']
})
export class MenuUpdateInParkingComponent implements OnInit {
  body;
  head = ['', '', '', '', '', ''];

  constructor(private inpark: InparkdbService,private parking: ParkingService,private device: DeviceService,private road: RoadListDBService) { 
    this.inpark.inParkList$.subscribe((inList)=>{
      this.body = inList;
    });
  }

  ngOnInit() {
    this.inpark.getList();
  }
  addData(){
    let adminUser = JSON.parse(localStorage.adminUser);
    let comm = new Date().getTime();
    let data:inParkItem = {      
      adminUserId:adminUser.adminUserId,
      parkId:adminUser.parkId,
      createId:adminUser.createId,
      createName:adminUser.createName,
      createTime:adminUser.createTime, //+adminUser.createTime,
      updateTime:adminUser.updateTime, //+adminUser.updateTime,
      updateId:adminUser.updateId,
      updateName:adminUser.updateName,
      cardID:"1001", // ID
      cardNo:"2002", // 
      cardType:"", // 
      carNumber:"100010", // 
      ownerName:"hello", // 
      comeTime: comm, // ,number
      comePark:"1", // 
      gateNumber:"11s", // 
      comePhoto:"muyou", // 
      certificatePhoto:"muyou", // 
      cardBalance:1234, // 
      remark:"weibizhi", // 
      recordId:"10010", //       
    }
    this.inpark.addData(data);

  }

  clear(){
    this.inpark.clearDB();
  }

  sendData(newData){
    //
    if(newData){
      this.parking.addInParkList(this.body);
    }
    //    
    else{
      this.parking.addInParkList(this.body);
    }
  }

  delete(){
    this.inpark.deleteDatas("2002");
  }

}





